import React from "react";
import gitimg from '../img/git-hub.png';
import tstoryimg from '../img/tistory.png';

function Footer() {
    return (
        <div className="footer">
            <div className="footer_list">
                <div className="footer_ikon_list">
                    <div>
                        <img src={gitimg}></img>
                    </div>
                    <div>
                        <img src={tstoryimg}></img>
                    </div>
                </div>
                <p>© 2024. Yu Yeo Reum. All rights reserved.</p>
            </div>
        </div>
    )
}

export default Footer;


