let typingBool = false; 
let typingIdx = 0; 

let typingTxt = "Summer";
typingTxt = typingTxt.split(""); // 한글자씩 자른다. 

let tyInt; // 전역 변수로 선언

console.log(typingTxt);

function startTyping() {
  if (typingBool === false) { 
    // 타이핑이 진행되지 않았다면 
    typingBool = true;     
    tyInt = setInterval(typing, 200);
  } 
}

function typing() {
  if (typingIdx < typingTxt.length) {
    // 타이핑될 텍스트 길이만큼 반복
    document.querySelector(".typing").append(typingTxt[typingIdx]);
    // 한글자씩 이어준다.
    typingIdx++;
  } else {
    // 끝나면 반복종료
    clearInterval(tyInt);
  }
}



startTyping(); // 타이핑 시작